.custom-container {
  max-width: 1412px;
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-right: auto;
  margin-left: auto;
}
.page-header {
  vertical-align: middle;
  .bg-overlay {
    position: relative;
  }
  .custom-container {
    min-height: 430px;
    .bg-overlay {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      &::after {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        bottom: 0px;
        width: 70%;
        height: 100%;
        opacity: 0.6;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 99.43%);
      }
      .img-banner {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top;
      }
    }
  }
}
.page-header-2 {
  vertical-align: middle;
  .custom-container {
    min-height: 430px;
    .bg-overlay {
      position: absolute;
